.show{
    width: 100%;
    height: 100vh !important;
    padding-top: 80px;
}
.nav_background_change{
    background-color: black;
}
.main-img{
    background: url(/src/images/banner-main.png); 
}

/* nav_bar */
nav{
    position: fixed;
    top: 0;
    width: 100vw;
    transition: ease-in-out 0.5s;
    z-index: 10;
}
.nav_bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 117px 20px 117px;
}
.navlinks ul{
   display: flex;
}
.navlinks ul li{
   list-style: none;
   margin-right: 40px;
   font-family: 'Rajdhani', sans-serif;
}
.navlinks ul li a{
    color: #fff;
    font-size: 18px;
    text-decoration: none;
    /* font-weight: 600; */
}
.phone_info{
   display: flex;
   align-items: center;
}
.phone_img img{
    width: 48px;
    height: 48px;
    transition: .5s;
}
.phone_img img:hover{
    transform: rotateY(180deg)
}

.phone_text{
    margin-left: 10px;
    color: white;
}
.phone_text p{
    margin-bottom: 0;
    font-family: 'Rubik', sans-serif;
    font-size: 14px;
}
.phone_text h2{
    font-size: 17px;
    font-family: 'Rajdhani', sans-serif;
}

/* hamb */
.mobile_nav{
    display: none;
    justify-content: space-between;
    width: 100%;
}
.company_name_text h2{
    color: white;
    font-size: 22px;
    font-family: 'Rubik', sans-serif;
    font-weight: 800;
}
.hamb_bar_icon i{
    color: white;
    font-size: 30px;
}
/* nav animated line */
.line_cont{
    background-color: rgba(175, 173, 173, 0.673) ;
    height: 1px;
    width: 100%;
    overflow: hidden;
}
.line{
    width: 80px;
    height: 3px;
    background-color: #ef3729;
    position: absolute;
    animation: slide-right 10s linear infinite;
}




@media(max-width:1100px){
    .nav_bar {
        padding: 20px 70px 20px 70px;
    }
}
@media(max-width:1000px){
    .nav_bar{
        background: #E91F4F;
        padding: 20px 50px 20px 50px;
        position: relative;
        flex-direction: column-reverse;
    }
    .company_logo{
        display: none;
    }
    .navlinks{
        height: 0;
        overflow: hidden;
        transition: ease-in-out 1s;
    }
    .navlinks ul{
        flex-direction: column;
        align-items: center;
    }
    .navlinks ul li{
        margin-right: 0;
        margin-bottom: 40px;
    }
    .phone_info{
        display: none;
    }
    .mobile_nav{
        display: flex;
    }
}



