.other_page_hero_nav_bg{
    background: url(/src/images/bg-5.jpg);
    background-position: center;
}
.other_page_main_heading_cont{
    padding-top: 200px;
    padding-bottom: 160px;
}
.other_page_navbar_after{
    text-align:center ;
    font-family: 'Rajdhani', sans-serif;
}
 .other_page_navbar_after h1{
    font-size: 100px;
    color: white;
}
 .other_page_navbar_after a{
    color: white;
    font-size: 20px;
}
 .other_page_navbar_after a:hover{
    text-decoration: none;
}
 .other_page_navbar_after i{
    color: white;
    margin-left: 10px;
    margin-right: 10px;
}
 .other_page_navbar_after span{
    color: #aa3029;
    font-size: 20px;
}



@media(max-width:1300px){

    .other_page_navbar_after h1 {
        font-size: 70px;
    }
}

