.our_team_section{
    margin-top: 90px;
    margin-bottom: 90px;
}
.our_section_text_cont{
    text-align: center;
}



.team_name h3{
    font-size: 24px;
    font-family: 'Rajdhani', sans-serif;
}
.our_section_text_cont h5{
    display: inline-block;
}

.our_section_card_cont{
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.our_section_card{
    width: 270px;
    text-align: center;
    box-shadow: 0 4px 30px rgb(0 0 0 / 12%);
}
.our_section_card_img img{
    filter: grayscale(1);
    transition: ease-in-out 0.3s;
}
.our_section_card:hover .our_section_card_img img{
    filter: grayscale(0);
}
.team_name_and_contact{
    position: relative;
    bottom: 30px;
}
.team_name{
    box-shadow: 0 -13px 30px rgb(0 0 0 / 12%);
    background: white;
    text-align: center;
    padding: 13px 0px 1px;
    border-radius: 9px 0 0px 0;
    width: 206px;
    margin: auto;
}
.team_name p{
    font-size: 16px;
    font-family: rubik;
    color: #ED2921;
    margin-bottom: 16px;
}
/* .our_section_card_contact{
     text-align: center;
} */
.team_contact{
    padding-top: 5px;
}
.team_contact a{
    display: inline-block;
    position: relative;
    margin-right: 5px;
    padding: 8px 16px 8px;
    background: #f5f5f6;
    font-size: 16px;
    font-family: rubik;
    color: #9797a1;
    border-radius: 5px;
    margin-right: 10px;
    z-index: 1;
}
.team_contact a::after{
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    border-radius: 5px;
    background: #ED2A22;
    transform: scale(0);
    transition: 0.5s;
    z-index: -1;
}
.team_contact a:hover::after{
    transform: scale(1);
}
.team_contact a:hover{
    color: white;
    text-decoration: none;
}



@media(max-width:1300px){

    .our_section_card {
        width: 210px;
    }
    .our_section_card_img img {
        width: 210px;
    }
    .team_name_and_contact {
        bottom: 45px;
    }
    .team_name {
        width: 180px;
    }
    .team_name h3 {
        font-size: 20px;
    }
    .team_contact :nth-child(3) {
        margin: 0;
    }
}

@media(max-width:1100px){
}

@media(max-width:1000px){
    .our_section_card_cont {
        display: grid;
        grid-template-columns: 1fr 1fr;
        margin-top: 40px;
    }
    .our_section_card {
        width: 330px;
        margin-bottom: 40px;
    }
    .our_section_card_img img {
        width: 330px;
    }
    .team_name {
        width: 290px;
    }
    .team_name h3 {
        font-size: 22px;
    }
    
}

@media(max-width:800px){
    .our_section_card_cont {
        display: flex;
        flex-direction: column;
    }
    .our_section_card {
        width: 100%;
    }
    .our_section_card_img img {
        width: 100%;
    }
    .team_name {
        width: 470px;
    }
    
}

@media(max-width:600px){
    .team_name {
        width: 360px;
    }
}

@media(max-width:500px){

}
@media(max-width:400px){
    .team_name {
        width: 260px;
    }
}