@media(max-width:1300px){
    .container{
        max-width: 960px;
    }
    .nav_after_main_cont {
        padding-bottom: 180px;
    }
    .nav_after_text_cont h1{
        font-size: 80px;
        line-height: 68px;
    }
    .download_btn a {
        padding: 13px 26px;
    }
    .black_white_girl_img img{
        width: 508px;
    }
    .agency_name {
        top: 38%;
        right: -244px;
    }
    .figma_img {
        bottom: 42px;
        left: 28px;
    }
    /* homepage about section */

    .add_circle_line h1{
        font-size: 35px;
    }
    .homepage_about_section {
        width: 450px;
    }

   

    /* homepage choose us */
    .chooseus_left_img_cont img {
        width: 460px;
    }
    .chooseus_right_text_cont {
        width: 450px;
    }
    .reason_to_choose_left_number {
        width: 105px;
    }

    /* carousel */
    .our_projects_text_cont h1 {
        font-size: 44px;
    }
    .item img{
        height: 251px;
    }

    /* -our location-- ---> counter */
    .counter_block h1 {
        font-size: 62px;
    }
   /* ------>contact us */
   .our_location_cont {
        width: 537px;
    }
    .user_detail_cont {
        width: 400px;
        margin-left: 20px;
    }
    .user_contact_heading h2 {
        font-size: 32px;
    }
}

@media(max-width:1100px){
}

@media(max-width:1000px){
    .container{
        max-width: 720px;
    }

    /* ////////////////// */
    .main-img {
        background: none;
    }
    .nav_after_section{
        background: url(/src/images/banner-main.png); 
    }
    .nav_after_main_cont {
        flex-direction: column;
        padding-top: 60px;
        padding-bottom: 58px;
    }
    .nav_after_left_text_btn_cont {
        margin-top: 40px;
    }
    .nav_after_text_cont h1{
        font-size: 75px;
    }
    .black_white_girl_img img{
        width: 448px;
    }
    
    /* homepage about section */
    .homepage_about_section {
        width: 330px;
    }
    .add_circle_line h1 {
        font-size: 30px;
    }
    .homepage_about_text p {
        line-height: 30px;
        font-size: 15px;
    }

  

     /* homepage choose us */
    .chooseus_main_cont {
        align-items: center;
    }
    .chooseus_left_img_cont img {
        width: 370px;
    }  
    .chooseus_right_text_cont {
        width: 330px;
    }
    .reason_to_choose_left_number {
        width: 156px;
    }

    /* carousel */
    .our_projects_text_cont h1 {
        font-size: 26px;
    }

     /* -our location-- ---> counter */
     .counter_section {
        height: 730px;
    }
     .counter_row {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .counter_block {
        margin-bottom: 50px;
    }
    .our_location_cont {
        width: 390px;
    }
    .our_location_title p {
        font-size: 15px;
    }
    .user_detail_cont {
        width: 300px;
    }
    .user_contact_heading h2 {
        font-size: 24px;
    }
}

@media(max-width:800px){
    .container{
        max-width: 540px;
    }
    .nav_after_text_cont h1 {
        font-size: 55px;
        line-height: 50px;
    }
    .graph_animated_img {
        top: -33px;
        right: -89px;
    }

    /* homepage_about_section */
    .homepage_about_section {
        width: 100%;
    }
    .add_circle_line h1 {
        font-size: 28px;
    }

   

   /* homepage choose us */
    .chooseus_main_cont {
        flex-direction: column;
    }
    .chooseus_right_text_cont {
        width: 100%;
        margin-top: 40px;
    }
    .reason_to_choose_left_number {
        width: 85px;
    }

    /* -our location-- ---> counter */
    .counter_section {
        height: 1175px;
    }
    .counter_row {
        display: flex;
        flex-direction: column;
    }
    .our_location_section {
        height: 1282px !important;
    }
    .our_loction_and_user_detail_cont {
      flex-direction: column;
    }
    .our_location_cont {
        width: 100%;
        margin-bottom: 25px;
    }
    .user_detail_cont {
        width: 100%;
        margin: 0;
    }
}

@media(max-width:600px){
    .container{
        /* max-width: 540px; */
        padding-left: 20px;
        padding-right: 20px;
    }
    .nav_after_left_text_btn_cont {
        margin-top: 82px;
    }
    .nav_after_text_cont h1 {
        font-size: 40px;
        line-height: 40px;
    }
    .social_links{
        display: none;
    }

    /* homepage about section */
    .add_circle_line h1 {
        font-size: 26px;
    }

    /* homepage contact section */
    .homepage_services_text_right_cont p {
        font-size: 14px;
    }
    .home_cont_card_text h2 {
        font-size: 20px;
    }

      /* homepage choose us */
    .chooseus_right_first_cont p {
        font-size: 15px;
    }
    .reason_to_choose_right_text p {
        font-size: 14px;
    }

    /* -our location-- ---> counter */
    .counter_block h1 {
        font-size: 50px;
    }
    .our_details_text p {
        font-size: 13px;
    }
    .our_location_section{
        height: 100% !important;
        background-size: cover;
    }
    .our_loction_and_user_detail_cont {
        position: relative;
    }
}

