.services_you_can_get_bg{
  background: url(/src/images/techno-4-bg.jpg);
  background-size: cover;
  /* height: 800px; */
}
.services_you_can_get_main_cont{
    display: flex;
    justify-content: space-between;
    /* padding-top: 90px; */
}
.services_you_can_get_left_cont{
    width: 445px;
}
.services_you_can_get_left_cont p{
  font-size: 16px;
  font-family: rubik;
  margin-top: 15px;
}
.services_you_can_get_right_cont{
    width: 635px;
}
.services_you_can_get_right_btns ul{
    display: flex;
    position: relative;
    margin-bottom: 30px;
}
.services_you_can_get_right_btns ul li{
    list-style: none;
    margin-right: 45px;
    font-size: 18px;
    font-family: 'Rajdhani', sans-serif;
    padding: 15px 22px;
    background-color: #ed2921;
    border-radius: 5px 5px 0 0;
    color: white;
}
.services_you_can_get_right_btns ul::after{
    position: absolute;
    content: "";
    background-color: #ed2921;
    width: 100%;
    height: 3px;
    bottom: 0;
}
.services_change_cont p{
    font-size: 16px;
    font-family: rubik;
}
.services_change_image_and_text{
    display: flex;
    justify-content: space-between;
    margin-top: 15px;
}
.services_change_image img{
    width: 302px;
}
.services_change_image_and_text ul li{
    list-style: none;
    font-size: 17px;
    font-family: 'Rajdhani', sans-serif;
    padding-bottom: 13px;
}
.services_change_image_and_text ul li span{
   margin-right: 5px;
   color:  #ed2a22;
   font-size: 20px;
}


@media(max-width:1300px){

    /* services_second_section */
    .services_you_can_get_left_cont {
        width: 370px;
    }
    .services_you_can_get_left_cont h1{
        font-size: 35px;
    }
    .services_you_can_get_right_cont {
        width: 560px;
    }
    .services_you_can_get_right_btns ul li {
        font-size: 14px;
    }
    .services_change_image_and_text{
        margin-top: 35px;
    }
    .services_change_image img {
        width: 250px;
    }
    .services_change_image_and_text ul li {
        font-size: 14px;
    }
}

@media(max-width:1100px){
}

@media(max-width:1000px){

    /* services_second_section */
    .services_you_can_get_main_cont {
        flex-direction: column;
    }
    .services_you_can_get_left_cont {
        width: 100%;
    }
    .services_you_can_get_right_cont {
        width: 100%;
        margin-top: 50px;
    }
    .services_change_cont p {
        font-size: 14px;
    }
    .services_change_image_and_text {
      flex-direction: column;
    }
    .services_change_image{
        margin-bottom:20px ;
    }
    
}

@media(max-width:800px){

     /* services_second_section */
    .services_you_can_get_right_btns ul li {
        padding: 15px 20px;
    }
}

@media(max-width:600px){
    /* services_second_section */
    .services_you_can_get_right_btns ul li {
        padding: 15px 12px;
        margin-right: 22px;
    }
}

@media(max-width:500px){

    /* services_second_section */
    .services_you_can_get_left_cont p {
        font-size: 14px;
    }
    .services_you_can_get_right_btns ul {
        justify-content: space-between;
    }
    .services_you_can_get_right_btns ul :nth-child(3) {
        margin-right: 0px;
    }
}
@media(max-width:400px){

      /* services_second_section */
  
      .services_you_can_get_right_btns ul {
        flex-wrap: wrap;
    }
    .services_you_can_get_right_btns ul :nth-child(3) {
        margin-top: 10px;
    }
}