.about_vedio_cont {
    /* display: flex; */
    /* flex-direction: column; */
    /* justify-content: center; */
    text-align: center;
}
.about_video_text{
    margin-bottom: 30px;
}
.about_video_text h5::before {
    left: 518px;
}
.about_video_text h5::after {
    left: 600px;
}
.video{
    background: url(/src/images/video.png);
    background-position: center center;
    height: 530px;
    width: 100%;
    border-radius: 10px;
}

.video_text h2{
    font-size: 40px;
    font-family: 'Rajdhani', sans-serif;
    color: white;
    line-height: 50px;
}


.video_position_cont{
    padding: 215px 0 222px;
    float: right;
}

.video_btn{
    margin-top: 10px;
}
.video_btn a{
    background: white;
    display: inline-block;
    color: black;
    text-decoration: none;
    padding: 14px 35px;
    border-radius: 5px;
    font-family: 'Rajdhani', sans-serif;
    font-size: 18px;
    position: relative;
    z-index: 1;
    transition: .5s;
}
.video_btn a::after{
    position: absolute;
    content: "";
    left: 0;
    z-index: -1;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    transform: scaleX(0);
    background: black;
    border-radius: 5px;
    transition: .5s;
}
.video_btn a:hover::after{
    transform: scale(1);
    /* width: 100%; */
}


.video_btn a:hover{
    color: white;
}
.video_btn a i{
    margin-left: 10px;
    color: #ED411D;
    font-size: 25px;
}


/* testi_section */
.testi_section{
    margin-top: 90px;
    padding-top: 90px;
    background: #f3f7fa;
}
.testi_main_heading {
    text-align: center;
}
.testi_main_heading h5::before {
    left: 500px;
}
.testi_main_heading h5::after {
    left: 628px;
}

/* card */

.testi_cards_container{
    margin-top: 80px;
    display: flex;
    justify-content: space-around;
}
.testi_card{
    padding: 32px 38px 30px;
    box-shadow: 0px 0px 10px 0px rgb(0 0 0 / 15%);
    background: #fff;
    width: 520px;
    border-radius: 5px;
}
.testi_img_and_name{
    display: flex;
    align-items: center;
}
.testi_name{
    margin-left: 20px;
}
.testi_name h2{
    font-family: 'Rajdhani', sans-serif;
    font-size: 24px;
}
.testi_name span{
    font-size: 16px;
    font-family: rubik;
    color: #ED2A22;
}

.testi_para_review{
    margin-top: 20px;
}
.testi_para p{
    font-size: 16px;
    font-family: rubik;
    color: #33354b;
}
.testi_star i{
    color: #ED2A22;
    margin-right: 5px;
}

