.contact_form_section{
    margin-top: 90px;
    margin-bottom: 90px;
}

.contact_us_heading_cont{
    text-align: center;
}
.contact_us_heading_cont h5{
    display: inline-block;
    
}


.our_information_and_user_form_cont{
    display: flex;
    margin-top: 50px;
}
.our_info_card{
    display: flex;
    background: #f3f7fa;
    padding: 32px 30px 32px;
    width: 350px;
    margin-bottom:30px ;
}
.our_info_text{
    margin-left: 20px;
}
.our_info_text h2{
    font-family: 'Rajdhani', sans-serif;
    font-size: 24px;
}
.our_info_text p{
    font-size: 16px;
    font-family: rubik;
}

.user_form{
    margin-left: 30px;
    background: #f3f7fa;
    width: 100%;
    padding: 34px 50px 30px;
    border-radius: 8px;
}
.user_form h4{
    font-size: 19px;
    font-family: 'Rajdhani', sans-serif;
}
.form_inputs{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 30px;
}
.form_inputs span{
    display: block;
    font-size: 16px;
    color: black;
    font-family: rubik;
    margin-bottom: 10px;
}
.form_inputs input{
    padding: 15px 20px;
    width: 84%;
    margin-bottom: 30px;
    border: none;
}

.user_message_and_btn textarea{
    width: 100%;
    border: 0;
    padding: 15px 22px 0px;
}
.user_message_and_btn a{
    margin-top: 40px;
    background: linear-gradient(to right, #ed2b22 0%, #ed431d 100%);
    display: inline-block;
    color: white;
    text-decoration: none;
    padding: 18px 35px;
    border-radius: 5px;
    font-family: 'Rajdhani', sans-serif;
    font-size: 18px;
    position: relative;
    z-index: 1;
    transition: .5s;
}
.user_message_and_btn a i{
    margin-left: 5px;
}
.user_message_and_btn a ::after{
    position: absolute;
    content: "";
    left: 0;
    z-index: -1;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    background: #fff;
    transform: scale(0);
    border-radius: 5px;
    transition: .5s;
}
.user_message_and_btn a:hover ::after{
    transform: scale(1);
}
.user_message_and_btn a:hover{
    color: #ED2D22;
}





@media(max-width:1300px){

    .our_info_text h2 {
        font-size: 20px;
    }
    .our_info_text p {
        font-size: 14px;
        font-family: rubik;
    }
    .user_message_and_btn a {
        padding: 13px 30px;
    }
}

@media(max-width:1100px){
}

@media(max-width:1000px){

    .our_information_and_user_form_cont {
        flex-direction: column;
    }
    .our_info_card {
        width: 100%;
    }
    .user_form {
        margin-left: 0px;
    }

   
}

@media(max-width:800px){

   
   
}

@media(max-width:600px){

    .form_inputs {
        display: flex;
        flex-direction: column;
    }
    .form_inputs input {
        width: 100%;
    }
}

