*{
    margin: 0;
    padding: 0;
}
body{
    user-select: none;
}
@keyframes figma-ani{
    0% {transform: translateY(0px); }
    50% {transform: translateY(-35px); }
    100% {transform: translateY(0px);} 
}
@keyframes slide-right {
    0% {transform: translateX(0);}
    50% {transform: translateX(94vw);}
    100% {transform: translateX(0vw);}
}
.container{
    max-width: 1140px;
    /* max-width: 1104px; */
    margin: auto;
}


/* nav_after_container :=> text and black_and_white_girl */

.nav_after_main_cont{
    display: flex;
    padding-top: 150px;
    padding-bottom: 70px;

}
.nav_after_left_text_btn_cont{
    /* width: 635px; */
    margin-top: 90px;
}
.nav_after_text_cont h1{
    font-size: 95px;
    font-family: 'Rajdhani', sans-serif;
    font-weight: 700;
    color: white;
    line-height: 80px;
    margin-top: 11px;
}
.nav_after_text_cont :nth-child(3){
    color: #EB401E;
}
.nav_after_text_cont p{
    font-size: 16px;
    color: white;
    font-family: 'Rubik', sans-serif;
    margin-top: 20px;
    line-height: 27px;
}
.download_btn{
    margin-top: 24px;
}
.download_btn a{
    background: linear-gradient(to right, #ed2b22 0%, #ed431d 100%);
    display: inline-block;
    color: white;
    text-decoration: none;
    padding: 18px 35px;
    border-radius: 5px;
    font-family: 'Rajdhani', sans-serif;
    font-size: 18px;
    position: relative;
    z-index: 1;
    transition: .5s;
}
.download_btn a i{
    margin-left: 5px;
}
.download_btn a ::after{
    position: absolute;
    content: "";
    left: 0;
    z-index: -1;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    background: #fff;
    transform: scale(0);
    border-radius: 5px;
    transition: .5s;
}
.download_btn a:hover ::after{
    transform: scale(1);
}
.download_btn a:hover{
    color: #ED2D22;
}

