.reason_to_choose_cont{
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.reason_to_choose_left_number{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 33px;
    width: 85px;
    height: 60px;
    background-color: #fff;
    border-radius: 50px;
    color: red;
    z-index: 1;
}
.reason_to_choose_left_number h3{
    transition: ease-in-out 0.4s;
}
.reason_to_choose_left_number::before{
    content: "";
    background-color: red;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 50px;
    transform: scale(0);
    z-index: -1;
    transition: ease-in-out 0.4s;
}
.reason_to_choose_left_number:hover::before{
    transform: scale(1);
}
.reason_to_choose_left_number:hover h3{
    color: #fff;
}
.reason_to_choose_right_text h4{
    font-size: 22px;
    color: #232323;
    font-family: 'Rajdhani', sans-serif;
    margin-bottom: 10px;
}
