.owl{
    margin-top: 50px;
}
.item{
    display: flex;
    justify-content: center;
    position: relative;
    margin-right: 30px;
    border-radius: 10px;
    overflow: hidden;
}
.item img{
    height: 376px;
    filter: grayscale(1);
    transition: ease-in-out 0.5s;
}
.item img:hover{
    filter: grayscale(0);
    transform: scale(1.1);
}
.item_text_cont{
    position: absolute;
    padding: 20px 0px;
    width: 272px;
    bottom: -90px;
    background-color: #ed2a22;
    border-radius: 5px;
    color: white;
    text-align: center;
    transition: ease-in-out 0.5s;
}
.item:hover .item_text_cont{
    bottom: 50%;
}
.item_text_cont h5{
    display: inline-block;
    position: relative;
    font-family: rubik;
    font-size: 12px;
}
.item_text_cont h5::after{
    position: absolute;
    display: block;
    content: "";
    height: 1px;
    width: 40px;
    bottom: 2px;
    left: -50px;
    background: #fff;
}
.item_text_cont h5::before{
    position: absolute;
    display: block;
    content: "";
    height: 1px;
    width: 40px;
    bottom: 2px;
    right: -50px;
    background: #fff;
}
.item_text_cont h2{
    font-family: 'Rajdhani', sans-serif;
    font-size: 18px;
}

