




/*  */

.it_solution_section{
    margin-top: 90px;
}



.it_soluction_cards_cont{
    display: flex;
    flex-wrap: wrap;
}
/* card */
.it_soluction_card{
    position: relative;
    width: 280px;
    padding: 44px 32px 5px;
    box-shadow: 0px 0 6px rgb(26 46 85 / 10%);
    text-align: center;
    z-index: 1;
}
.it_soluction_card::after{
    display: block;
    position: absolute;
    content: "";
    height: 0;
    bottom: 0;
    right: 0;
    left: 0;
    background: #ed2c41;
    z-index: -1;
    transition: ease-in-out 0.5s;
}
.it_soluction_card:hover::after{
    height: 100%;
}
.it_soluction_card:hover{
    color: white;
}
.it_soluction_card_img img{
    margin-bottom: 10px;
}
.it_soluction_card_text h2{
    font-family: 'Rajdhani', sans-serif;
    font-size: 24px;
    transition: ease-in-out 0.3s;
}
.it_soluction_card_text p{
    font-family: rubik;
    font-size: 16px;
    transition: ease-in-out 0.3s;
}


