.footer_and_copyright_bg{
    background: url(/src/components/globel/images/footer-bg.jpg);
    /* height: 800px;
    width: 100%; */
    padding-top: 200px;
    margin-top: 120px;
}

.footer{
    display: flex;
    justify-content: space-between;
    padding-bottom: 50px;
    margin-top: 100px;
}
.company_details{
    width: 360px;
}
.footer_company_text p{
    color: white;
    margin-top: 20px;
    font-family: rubik;
    font-size: 17px;
}
.footer_social_links{
    margin-top: 30px;
}
.footer_social_links ul{
    display: flex;
}
.footer_social_links ul li{
    position: relative;
    list-style: none;
    margin-right: 20px;
    padding: 10px 12px;
    border-radius: 5px;
    background-color: #292643;
    cursor: pointer;
    z-index: 1;
}
.footer_social_links ul li i{
    color: white;
    font-size: 20px;
}
.footer_social_links ul li ::after{
    position: absolute;
    content: "";
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    border-radius: 5px;
    background: #ed2c41;;
    transform: scale(0);
    transition: ease-in-out 0.3s;
    z-index: -1;
}
.footer_social_links ul li:hover ::after{
    transform: scale(1);
}

.footer_resource_and_useful_links{
    display: flex;
}
/* .footer_resource_and_useful_links :nth-child(2){
    margin: 0;
} */
.footer_resource_links{
    margin-right: 40px;
}
.footer_resource_links span{
    color: #ed2c41;
}
.footer_resource_links h1{
    color: white;
    font-family: 'Rajdhani', sans-serif;
}
.footer_resource_links ul{
    margin-top: 20px;
}
.footer_resource_links ul li{
    display: flex;
    align-items: center;
    list-style: none;
    margin-bottom: 13px;
}
.footer_resource_links ul li::before{
    display: inline-block;
    content: "";
    width: 10px;
    height: 1px;
    margin-right: 10px;
    background-color: white;
}
.footer_resource_links ul li a{
    text-decoration: none;
    color: white;
    font-family: rubik;
    font-size: 16px;
    transition: ease-in-out 0.3s;
}
.footer_resource_links ul li a:hover{
    color: #ed2c41;
}

.footer_contact_details span{
    color: #ed2c41;
}
.footer_contact_details h1{
    color: white;
    font-family: 'Rajdhani', sans-serif;
}
.footer_contact_details ul li{
    display: flex;
    align-items: center;
    list-style: none;
    color: white;
    font-size: 16px;
    font-family: rubik;
    margin-top: 30px;
}
.footer_contact_details ul li i{
    color: #ed2c41;
    font-size: 30px;
    margin-right: 20px;
}

/* copyright */
.copyright_container{
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
   color: white;
   font-family: rubik;
   border-top: 1px solid #ed2c41;
   padding: 20px;
}
.copyright_from_to_to h4 span{
    color: #ed2c41;
}


.newsletter_cont{
    background: url(/src/components/globel/images/counter-bg.jpg);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px 50px 38px;
    border-radius: 10px;
    margin-top: -280px;
}
.newsletter_text{
    color: white;
}
.newsletter_text h1{
    font-family: 'Rajdhani', sans-serif;
    font-size: 45px;
}
.newsletter_text p{
    font-family: rubik;
    font-size: 16px;
}

.newsletter_email_input input{
    padding: 20px 30px;
    border-radius: 5px;
    border: 0;
    font-family: 'Rajdhani', sans-serif;
    font-size: 17px;
}
.newsletter_email_input :nth-child(1){
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}
.newsletter_email_input :nth-child(2){
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    background: #232434;
    color: white;
    cursor: pointer;
}


@media(max-width:1300px){
    .newsletter_text h1 {
        font-size: 38px;
    }
    .newsletter_text p {
        font-size: 14px;
    }
    .newsletter_email_input input {
        padding: 15px 17px;
    }
    .company_details {
        width: 275px;
    }
    .footer_company_text p {
        font-size: 14px;
    }
    .footer_social_links ul li {
        padding: 8px 11px;
    }
    .footer_resource_links h1 {
        font-size: 27px;
    }
    .footer_resource_links ul li a {
        font-size: 14px;
    }
    .footer_contact_details h1 {
        font-size: 27px;
    }
    .footer_contact_details ul li {
        font-size: 14px;
    }
    .footer_contact_details ul li i {
        font-size: 25px;
    }
    .copyright_container h4{
        font-size: 15px;
    }
}

@media(max-width:1100px){
}

@media(max-width:1000px){
    .newsletter_cont {
        padding: 40px 25px 38px;
    }
    .footer_and_copyright_bg {
        margin-top: 120px;
        
    }
    .newsletter_text h1 {
        font-size: 27px;
    }
    .newsletter_email_input input {
        padding: 15px 10px;
    }

    .footer {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
    .footer_useful_links{
        margin-top: 30px;
    }
    .footer_contact_details{
        margin-top: 30px;
    }
}

@media(max-width:800px){
    .newsletter_cont {
       flex-direction: column;
       text-align: center;
       padding: 30px 25px 30px;
    }
    .newsletter_text p {
        margin-bottom: 20px;
    }
    .footer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .company_details {
        width: 100%;
        text-align: center;
        margin-bottom: 20px;
    }
    .footer_company_text p {
        font-size: 17px;
    }
    .footer_social_links ul {
        justify-content: center;
    }
    .footer_resource_links ul li a {
        font-size: 16px;
    }
    .copyright_container {
      flex-direction: column;
      align-items: center;
    }
    .copyright_from_to_to :nth-child(1){
        margin-bottom: 20px;
    }
}

