@media(max-width:500px){
    .newsletter_text h1 {
        font-size: 18px;
    }
    .newsletter_text p {
        margin-bottom: 13px;
    }
    .newsletter_email_input input {
        padding: 15px 5px;
    }
    .copyright_container h4 {
        font-size: 10px;
    }
}
