.homepage_services_bg_img{
    background: url(/src/images/black-gray-graphic.jpg);
}
.homepage_services_text_main_cont{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.homepage_services_text_left_cont h1{
    color: white;
}
.homepage_services_text_right_cont{
    width: 545px
}
.homepage_services_text_right_cont p{
    color: white;
    font-size: 16px;
    line-height: 28px;
    font-family: rubik;
}

.home_contact_card_cont{
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
}
.home_cont_card{
    position: relative;
    background: #19191b;
    padding: 40px 38px 30px;
    width: 255px;
    border-radius: 5px;
    z-index: 5;
}
.home_cont_card::after{
    position: absolute;
    content: "";
    left: 0;
    z-index: -2;
    right: 0;
    bottom: 0;
    height: 0px;
    margin: auto;
    background: #ed2c41;
    border-radius: 5px;
    transition: .5s;
}
.home_cont_card:hover::after{
    height: 100%;
}
.home_cont_card:hover .home_cont_card_text :nth-child(2)::after{
    background: white;
}
.home_cont_card:hover .home_cont_card_readmore a{
    color: white;
}
.home_cont_card:hover .home_cont_card_readmore a::after{
    height: 35%;
    width: 50%;
}
.home_cont_card_img{
    display: flex;
    height: 90px;
    width: 90px;
    justify-content: center;
    align-items: center;
    background-color: #e91f4f;
    border-radius: 50px;
}
.home_cont_card_img img{
    height: 52px;
}
.home_cont_card_text{
    margin-top: 24px;
    color: white;
}
.home_cont_card_text h2{
    font-family: 'Rajdhani', sans-serif;
    line-height: 1.3;
}
.home_cont_card_text :nth-child(2){
    margin-bottom:20px;
}
.home_cont_card_text :nth-child(2)::after{
    display: block;
    content: "";
    height: 2px;
    width: 50px;
    margin-top: 20px;
    background: #EB401E;
    transition: .5s;
}
.home_cont_card_text p{
    font-family: rubik;
    font-size: 16px;
    margin-bottom: 16px;
}
.home_cont_card_readmore a{
    /* position: relative; */
    font-family: 'Rajdhani', sans-serif;
    font-size: 16px;
    color: #EB401E;
    transition: .7s;
}
.home_cont_card_readmore a i{
    margin-left: 10px;
    text-decoration: none;
}
.home_cont_card_readmore a:hover{
    text-decoration: none;
}
.home_cont_card_readmore a::after{
    position: absolute;
    content: "";
    z-index: -1;
    background: #dd1b13;
    height: 0;
    width: 0;
    bottom: 0;
    left: 0;
    clip-path: polygon(0% 0%, 100% 100%, 100% 100%, 0% 100%);
    transition: 1s;
}



@media(max-width:1300px){

    /* homepage services section */
    .home_cont_card {
        width: 210px;
    }
    .home_cont_card_text h2{
        font-size: 18px;
    }
    .home_cont_card_text p{
        font-size: 14px;
        line-height: 30px;
    }

}

@media(max-width:1100px){
}

@media(max-width:1000px){

    /* homepage services section */
    .homepage_services_text_right_cont {
        width: 306px;
    }
    .home_contact_card_cont {
        display: grid;
        grid-template-columns: 1fr 1fr;
    }
    .home_cont_card {
        width: 330px;
        margin-bottom: 30px;
    }
    .home_cont_card_text h2 {
        display: inline;
        font-size: 22px;
    }
    .home_cont_card_text :nth-child(2) {
        margin-left: 5px;
    }
    .home_cont_card_text p {
        font-size: 16px;
        margin-top: 20px;
    }

}

@media(max-width:800px){

    /* homepage_services section */
    .homepage_services_text_main_cont {
        flex-direction: column;
        align-items: start;
    }
    .homepage_services_text_right_cont {
        width: 100%;
        margin-top: 30px;
    }
    .home_contact_card_cont {
        display: block;
    }
    .home_cont_card {
        width: 100%;
    }
    .home_cont_card_text h2 {
        font-size: 24px;
    }
    .home_contact_card_cont :nth-child(4){
        margin-bottom: 0px;
    }


}

